
import { Component, ViewChild } from '@angular/core';
import { ChildComponent } from './child.conponets'
@Component({
    selector: 'app-parent',

    // template: `
    // <h3>in parent{{value}}</h3>
    // <app-child (myClick)="changeValue($event)"></app-child>
    // <button>AddInParent</button>
    // `
    // template: `
    // <button (click)="child.value=child.value+1">AddInParent</button>
    // <app-child #child></app-child>
    // `
    template: `
    <button (click)="onAddForChild();">AddInParent</button>
    <app-child></app-child>
    `
})
export class ParentComponent {
    value = 0
    changeValue(isAdd: boolean) {
        if (isAdd) { this.value++; }
        else {
            this.value--
        }

    }
   
    @ViewChild(ChildComponent)
    myChild: ChildComponent;
    onAddForChild() {
        this.myChild.value++;
    }
}